















import Vue from "vue";
import AwardBox from "../../components/AwardBox.vue";
export default Vue.extend({
    name : "Awards",
	components: {
		AwardBox
	},
	data() {
		return {
			awards: []
		};
	},
	created() {
		this.$store
			.dispatch("GET_CLUB_AWARDS")
			.then(awards => {
				this.awards = awards;
			})
			.catch(err => {});
	},
	computed: {
		getClub() {
			return this.$store.state.club;
		},
		getClubImage() {
			if (this.$store.state.club.imgPath)
				return (
					this.$store.state.mainPath + this.$store.state.club.imgPath
				);
			else
				return "https://pbs.twimg.com/profile_images/770139154898382848/ndFg-IDH_400x400.jpg";
		}
	}
});
