






import Vue from "vue";
import Post from "../../components/Post.vue";
export default Vue.extend({
    name : "Timeline",
	components: {
		Post
	},
	data() {
		return {
			posts: []
		};
	},
	created() {
		this.$store
			.dispatch("GET_CLUB_POSTS")
			.then(posts => {
				this.posts = posts;
			})
			.catch(err => {});
	}
});
